*{
    box-sizing: border-box;
}


body{
    margin:0;
    padding:0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3f7;
    height: 100%
}

htmlm, body, .constainer{
    height: 100%;
    display:flex;
    justify-content: center;
}

.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

}

.movies{
    display: flex;
    justify-content: space-between;
    align-items: flex-center;
    flex-wrap:wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%
}

.movies .movie{
    width: 45%;
    background-color:white;
    margin:50px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    font-weight: 300;
    padding: 20px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50, 0, 93, 0.25);
}

.movie img {
    max-width: 120px;
    top: -50px;
    margin-right: 30px;
    margin-left: 20px;
    position: relative;
}

.movie .movie__title, 
.movie .movie__year{
    margin:0;
    font-weight: 300;
}


.movie .movie__title {
    margin-bottom: 5px;
    font-size: 24px;
}

.movie .movie__year,
.movie .movie__genres {
    color: #d2d2e2
}
